.rank_wrap{
    width: 55%;
    position: absolute;
    top: 250px;
    left: 20px;
    background: #fff;
    box-shadow: 2px 2px 7px 2px rgba(0,0,0,0.2);
    padding: 20px 0;


.table{
    display: flex;
    flex-direction: column;
}

.table li{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 10px;

    figure{
        width: 45px;
    }

    h6{
        font-size: 14px;
        color: #444;
    }
    
    figcaption{
        font-size: 12px;
        font-family: "AppleSDGothicNeoM";
        color: #555;
    }
}

.table li:first-child{
    margin-top: 20px;
}

.table li:last-child{
    margin-bottom: 0;
}
 
.rankClose{
    position: absolute;
    right: 10px;
    top: 10px;

    img{
        width: 17px;
    }
}

}